<template>
  <div class="am-hello">

	  <!-- navbar 导航栏-->
    <!-- <div class="am-search search">
      <div class="left"></div>
      <div class="center">社区</div>
      <div class="right">
        <i class="iconfont" @click='search'>&#xe624;</i>
      </div>
    </div> -->

      <!-- sort 排序-->
      <!-- <div class="am-sort" @click.stop="showSortBarHandler">
        {{value1|fb}}
        <i class="iconfont">&#xe6b0;</i>
      </div> -->


     <!-- <div :class="['am-sortBar', 'sortBar', showSortBar == true ?'showSortBar':'' ]" >
          <span @click="sort('article_comment_time')" :class="[value1 == 'article_comment_time' ?'active':'']">最新评论</span>
          <span @click="sort('article_publish_time')" :class="[value1 == 'article_publish_time' ?'active':'']">最新文章</span>
       </div> -->


<div class="sortBoss">
      <!-- 排序栏-->
      <div :class="['sortBar', showSortBar == true ?'showSortBar':'' ]" >
          <span @click="sort('article_comment_time')" :class="[value1 == 'article_comment_time' ?'active':'']">最新评论</span>
          <span @click="sort('article_publish_time')" :class="[value1 == 'article_publish_time' ?'active':'']">最新文章</span>
      </div>
      <!-- 当前排序-->

        <div class="sort" @click.stop="showSortBarHandler">
          {{value1| fNewAOrC}}
          <i class="iconfont">&#xe61a;</i>
        </div>

    </div>







      <!-- 下拉刷新  -->
	  <van-pull-refresh v-model="isLoading" @refresh="onRefresh" success-text="刷新成功"
	  class="refresh" :style = '{minHeight:winWH.height}'>
    <Banner></Banner>

    <div v-if="sortSwitch">
        <!-- 宫格 -->
          <Grid></Grid>
          <!-- 通知栏 -->
          <Notice></Notice>
          <!-- 列表 -->
          <List></List>
      </div>

	  </van-pull-refresh>
  </div>
</template>

<script>
  import Notice from "@/components/utils/Notice"
  /* 引入宫格组件*/
  import Grid from "@/components/utils/Grid"
  /* 轮播*/
  import Banner from "@/components/utils/Banner"
  /* 引入列表组件*/
  import List from "@/components/utils/List"
  import {createNamespacedHelpers} from 'vuex'
  const list=createNamespacedHelpers('list')
  const notice=createNamespacedHelpers('notice')
export default {
  name: '',
  // beforeRouteLeave(to,from,next ){
  //   if(to.path == '/plate'){
  //     to.meta.rank = 'clear'
  //   }
  // },
  data () {
    return {
      // 选项框
      showSortBar : false,
      //排序切换
      sortSwitch : true,
      // isLoading : false
    }
  },
  filters:{
    fb : function(value){
      //最新文章
      if(value == 'article_publish_time'){
        return '最新文章'
      }
      if(value == 'article_comment_time'){
        return '最新评论'
      }
    }
  },
  methods:{
    //展示选项框
    showSortBarHandler(){
      this.showSortBar = !this.showSortBar
    },
    //排序
    sort(arg){
      if(this.value1 == arg){
        this.showSortBar = !this.showSortBar
        return;
      }else{
        this.value1  = arg
      }
      // switch(arg){
      //   case 'article_comment_time':
      //     this.value1  = 'article_comment_time'
      //   break;
      //   case 'article_publish_time':
      //     this.value1  = 'article_publish_time'
      //   break;
      //   }
      this.showSortBar = !this.showSortBar
      this.sortSwitch = false
      this.$nextTick(() => {
        this.sortSwitch = true
      })
    },
    search(){
        this.$router.push({
          path:"/search"
        })
    },
    ...notice.mapMutations({
          	noticeArrHandler: 'noticeArrHandler'
          }),
    // ...list.mapMutations({
    //       	oneHandler: 'oneHandler'
    //       }),
    tosearch(){
      this.$router.push({
        path:"/search"
      })
    },
	// onRefresh(){
    onRefresh(){
      this.isLoading = true
      this.sortSwitch = false
      this.$nextTick(() => {
        this.sortSwitch = true
        // this.isLoading = false
      })
    }
	// }
  },
  computed:{
    value1:{
      get () {
        return this.$store._modulesNamespaceMap['list/'].context.state.value1
       },
      set (value) {
      this.$store._modulesNamespaceMap['list/'].context.commit('value1Handler', value)
      }
    },
    isLoading:{
      get () {
        return this.$store._modulesNamespaceMap['list/'].context.state.isLoadingForHomePage
       },
      set (value) {
      this.$store._modulesNamespaceMap['list/'].context.commit('isLoadingForHomePageHandler', value)
      }
    },
    winWH:{
      get () {
            return this.$store._modulesNamespaceMap['win/'].context.state.winWH
      },
      set (value) {
        this.$store._modulesNamespaceMap['win/'].context.commit('winWHHandler', value)
      }
    },
  },
  components : {
  	/* 这个组件名字不能和原生标签名重复 */
    Grid,
    List,
    Notice,
    Banner
  },
  mounted() {
    // this.$api.article.getNowBidding({}).then(
    //     res =>{
    //       // if(res.data.bidding_price){
    //       //   this.bidding_price = res.data.bidding_price
    //       // }
    //       this.noticeArrHandler(res.data.result)
    //     }
    // )
  },
  // 监听
  watch:{
    // '$route.path': {
    //     handler(newVal, oldVal) {
    //       console.log(newVal)
    //       switch(newVal){
    //         case '/':
    //             this.oneHandler(['homePage','more'])
    //             break;
    //       }
    //     },
    //     immediate: true,
    //     // deep: true
    // }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  /* 文章排序条*/
  .sortBoss{
    // background-color: red;
    position: fixed;
    right: 200px;//2
    width: 0;//1
    bottom : 200px;
    z-index: 99999999999999999999999999;
    /* 固定的 固定的*/
    .sort{
      position: relative;
      @include flex(row,flex-end,center)

      box-shadow: 5px 5px 10px #333;
      color: white;
      border-radius: 50px;
      font-size: 30px;
      font-weight: 500;
      @include wh(200px,80px);
      background-color: $green;
      @include flex(row,center,center)
      i{
        font-size:30px
      }
    }


    /* 弹出框*/
    .sortBar{
      position: relative;
      @include flex(row,flex-end,center)

      width: 200px;
      // padding: 0 25px;
      height: 0;
      transition: height .1s;
      overflow: hidden;
      font-size: 30px;
      background-color: #DCDCDC;
      color:rgb(152,152,152);
      @include flex(column,space-around,center)
      span{
        flex-grow : 1;
        @include wh(146px,60px)
        @include flex(row,center,center)
      }
    }
    /* 排序兰的div 显示时候的样式*/
    .showSortBar{
      height: 200px;
    }
    /* 选中的文字样式*/
    .active{
      color:$green;
    }

  }
</style>



